@import "../mixin.scss";
@import "../../../../../animations.scss";

.container{
	@include card;

	* {
		box-sizing: border-box;
	}

	.wrapper{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		padding: 10px;

		> * {
			width: 100%;
		}

		.roomStatusContainer {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%, -50%);
			.talking{
				background: #1174DC;
				font-weight: 500;
				font-size: 	13px;
				line-height: 12px;
				border-radius: 6px;
				color: #fff;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				width: max-content;
				padding: 6px 10px 6px 8px;
				min-width: 80px;

				img{
					width: 12px;
					margin-right: 8px;
				}

				.talkingAnimation{
					margin-right: 5px;
					display: flex;
					align-items: center;

					span{
						color: transparent;
						position: relative;
						width: 3px;
						height: 3px;
						border-radius: 50%;
						background: #fff;
						margin-right: 2px;
						animation: upAndDown linear 1.2s infinite;
						display: inline-block;

						&:nth-child(2){
							animation-delay: .2s;
						}

						&:nth-child(3){
							animation-delay: .4s;
						}
					}
				}
			}
			.locking{
				display: flex;
				justify-content: center;
				align-items: center;
				width: 24px;
				height: 24px;
				background: #00BC6D;
				border-radius: 6px;
				margin-left: 4px;

				img {
					width: 11px;
					height: 13px;
				}
			}
		}


		.users{
			padding: 10px 14px 5px;

			.userName{
				align-self: start;
				height: 100%;
				font-size: 18px;
				line-height: 26px;
				display: block;
				font-weight: 500;
				text-transform: capitalize;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;

				&:not(:first-child){
				}
			}

			.remainingUsers{
				color: #9c9c9c;
				font-size: 14px;
				line-height: 14px;
				margin-top: 5px;
			}
		}

		.actions{
			display: flex;
			align-items: flex-end;

			.button{
				border: 0;
				border-radius: 12px;
				font-size: 16px;
				font-weight: 500;
				height: 42px;
				overflow: hidden;
				margin: 0;
				padding: 0;

				&:hover{
					background: rgba(17, 116, 220, 0.14);
				}

				&.join {
					background: rgba(17, 116, 220, 0.07);
					color: #1174DC;
				}

				&.lock {
					background: #F5F5F5;
					color: #9C9C9C;
				}
			}
		}
	}
}